// –ö–æ–º–ø–æ–Ω–µ–Ω—Ç NFT –∫–∞—Ä—Ç–æ—á–∫–∏ —Å 3D-–∞–Ω–∏–º–∞—Ü–∏–µ–π (–±–µ–∑ –∏–∑–º–µ–Ω–µ–Ω–∏–π)
function NFTCard({ nft, getCategoryEmoji }: { nft: NFT, getCategoryEmoji: (category: string) => string }) {
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const [isHovered, setIsHovered] = useState(false);

  const handleMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
    const rect = e.currentTarget.getBoundingClientRect();
    const x = ((e.clientX - rect.left) / rect.width - 0.5) * 20; // ¬±10 –≥—Ä–∞–¥—É—Å–æ–≤
    const y = ((e.clientY - rect.top) / rect.height - 0.5) * -20; // ¬±10 –≥—Ä–∞–¥—É—Å–æ–≤
    
    setMousePosition({ x, y });
  };

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.5 }}
      className="relative group"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => {
        setIsHovered(false);
        setMousePosition({ x: 0, y: 0 });
      }}
      onMouseMove={handleMouseMove}
    >
      {/* –ö–∞—Ä—Ç–æ—á–∫–∞ —Å 3D-—ç—Ñ—Ñ–µ–∫—Ç–æ–º */}
      <motion.div
        animate={{
          rotateY: isHovered ? mousePosition.x : 0,
          rotateX: isHovered ? mousePosition.y : 0,
          y: isHovered ? -8 : 0,
          scale: isHovered ? 1.02 : 1,
        }}
        transition={{ type: "spring", stiffness: 300, damping: 20 }}
        className={`relative bg-gradient-to-br ${nft.gradient} border-2 ${nft.borderColor} rounded-3xl p-6 shadow-lg hover:shadow-2xl transition-shadow duration-300 overflow-hidden cursor-pointer`}
        style={{
          transformStyle: 'preserve-3d',
          transformOrigin: 'center center',
        }}
      >
        {/* –ò–Ω–¥–∏–∫–∞—Ç–æ—Ä —Ä–µ–¥–∫–æ—Å—Ç–∏ */}
        <div className="absolute top-4 right-4 z-10">
          <span className={`px-3 py-1 rounded-full text-xs font-bold ${rarityColors[nft.rarity]}`}>
            {nft.rarity.toUpperCase()}
          </span>
        </div>

        {/* –≠–º–æ–¥–∑–∏ –ø–∏—Ç–æ–º—Ü–∞ */}
        <div className="relative z-10 mb-6">
          <div className="w-24 h-24 mx-auto bg-white/30 rounded-2xl flex items-center justify-center backdrop-blur-sm">
            <span className="text-5xl">{nft.emoji}</span>
          </div>
        </div>

        {/* –ö–æ–Ω—Ç–µ–Ω—Ç –∫–∞—Ä—Ç–æ—á–∫–∏ */}
        <div className="relative z-10">
          <h3 className="text-xl font-bold text-gray-900 mb-2">{nft.name}</h3>
          <p className="text-gray-600 text-sm mb-4 line-clamp-2">{nft.description}</p>
          
          <div className="flex items-center justify-between mb-4">
            <div>
              <div className="text-sm text-gray-500">–¶–µ–Ω–∞</div>
              <div className="text-lg font-bold text-gray-900">{nft.price}</div>
            </div>
            <div className="text-right">
              <div className="text-sm text-gray-500">–í–ª–∞–¥–µ–ª–µ—Ü</div>
              <div className="text-sm font-medium text-purple-600">{nft.owner}</div>
            </div>
          </div>

          {/* –ö–∞—Ç–µ–≥–æ—Ä–∏—è */}
          <div className="inline-flex items-center gap-2 px-3 py-1.5 bg-white/50 rounded-full">
            <span className="text-sm">{getCategoryEmoji(nft.category)}</span>
            <span className="text-sm text-gray-700">{nft.category}</span>
          </div>
        </div>

        {/* –ö–Ω–æ–ø–∫–∞ –ø–æ–∫—É–ø–∫–∏ */}
        <motion.button
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          className="w-full mt-6 py-3 bg-gradient-to-r from-gray-900 to-gray-700 text-white font-medium rounded-xl hover:shadow-lg transition-shadow relative z-10"
        >
          –ö—É–ø–∏—Ç—å —Å–µ–π—á–∞—Å
        </motion.button>

        {/* –≠—Ñ—Ñ–µ–∫—Ç —Å–≤–µ—á–µ–Ω–∏—è –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */}
        <div className="absolute inset-0 bg-gradient-to-br from-white/0 via-white/0 to-white/0 group-hover:from-white/5 group-hover:via-white/10 group-hover:to-white/5 transition-all duration-300 rounded-3xl" />
        
        {/* –ê–Ω–∏–º–∏—Ä–æ–≤–∞–Ω–Ω—ã–µ —á–∞—Å—Ç–∏—Ü—ã */}
        {isHovered && (
          <>
            <motion.div
              initial={{ scale: 0, opacity: 0 }}
              animate={{ scale: 1, opacity: 0.3 }}
              transition={{ delay: 0.1 }}
              className="absolute top-1/4 left-1/4 w-4 h-4 bg-purple-300 rounded-full blur-sm"
            />
            <motion.div
              initial={{ scale: 0, opacity: 0 }}
              animate={{ scale: 1, opacity: 0.3 }}
              transition={{ delay: 0.2 }}
              className="absolute top-1/3 right-1/4 w-3 h-3 bg-pink-300 rounded-full blur-sm"
            />
          </>
        )}
      </motion.div>

      {/* –¢–µ–Ω—å –ø—Ä–∏ –ø–æ–¥–Ω—è—Ç–∏–∏ */}
      <motion.div
        animate={{
          scale: isHovered ? 0.95 : 1,
          opacity: isHovered ? 0.8 : 0,
        }}
        className="absolute inset-0 bg-gray-900/20 blur-xl rounded-3xl -z-10"
      />
    </motion.div>
  );
}